<template>
  <div id="add" class="container">
    <h1 class="page-header"></h1>
    <form v-on:submit="addCustomer">
      <div class="well">
        <h4></h4>
        <div class="form-group">
          <label></label>
          <input type="text" placeholder="" class="form-control" v-model="customer.name" required>
        </div>
        <div class="form-group">
          <label></label>
          <input type="tel" placeholder="" class="form-control" v-model="customer.phone" required>
        </div>
        <div class="form-group">
          <label></label>
          <input type="email" placeholder="" class="form-control" v-model="customer.email" required>
        </div>
        <div class="form-group">
          <label></label>
          <input type="text" placeholder="" class="form-control" v-model="customer.education">
        </div>
        <div class="form-group">
          <label></label>
          <input type="text" placeholder="" class="form-control" v-model="customer.graduatedSchool">
        </div>
        <div class="form-group">
          <label></label>
          <input type="text" placeholder="" class="form-control" v-model="customer.profession">
        </div>
        <div class="form-group">
          <label></label>
          <textarea placeholder="" class="form-control" v-model="customer.profile"></textarea>
        </div>
        <button type="submit" class="btn btn-default btn-primary"></button>
      </div>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "Add",
    data() {
      return {
        customer: {
          name: '',
          email: '',
          phone: '',
          education: '',
          graduatedSchool: '',
          profession: '',
          profile: ''
        }
      }
    },
    methods: {
      addCustomer(e) {
        let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (!myreg.test(this.customer.phone)) {
          alert("");
          e.preventDefault();
        } else {
          axios.post("/users", this.customer).then(() => {
            this.$router.push({path: "/", query: {alert: ""}});
          });
          e.preventDefault();
        }

      }
    }
  }
</script>

<style scoped type="text/less" lang="less">
  #add {
    textarea {
      height: 100px;
    }
  }
</style>



